<template>
  <el-row>
    <div style="height:300px">
      <el-input
        v-model="input"
        style="height:300px;width:100%; display:inline-block"
        placeholder="请输入密码"
      ></el-input>
    </div>

    <el-button type="success" plain @click="open" style="width:100%;height:100px">
      <h1>确定</h1>
    </el-button>
    <center>
      <el-link
        style="fontSize:20px;margin-top:400px"
        type="success"
        href="https://simplerandom.gitee.io/image/foreignwebsite.html"
      >如果你喜欢美图，点他！</el-link>
    </center>
  </el-row>
</template>
<script>
export default {
  data() {
    return {
      input: ""
    };
  },
  methods: {
    open() {
      if (this.input == "soul") {
        this.$router.push({
          path: "/person"
        });
      } else {
        this.$alert("密码不对", "警告", {
          confirmButtonText: "确定",
          callback: action => {
            this.$message({
              type: "info",
              message: "密码提示：四个小写字母，一款app"
            });
          }
        });
      }
    }
  }
};
</script>
<style lang="css">
</style>
