<template>
  <div>
    <audio class="audio" src="../assets/bg.mp3" controls autoplay="autoplay" hidden="true"></audio>
    <div id="demo">
      <div id="demoin">
        <div id="demo1">------------笑看人生，宁缺毋滥，希望小姐姐擦亮眼睛，不要遇到坏人！来自真的好喜欢小姐姐的某某于2020年2月28日凌晨的祝福！</div>
        <div id="demo2"></div>
      </div>
    </div>
    <el-row>
      <el-col :span="8" v-for="(o, index) in 520" :key="o" :offset="index > 0 ? 520: 0">
        <el-card :body-style="{ padding: '0px' }">
          <img src="../assets/henmei.png" class="image" />
          <div style="padding: 14px;">
            <span>{{index+1}}--------真的很美,真的！</span>
            <div class="bottom clearfix">
              <time class="time">{{ currentDate }}</time>

              <el-button type="success" class="button">保护好自己，希望你开心快乐每一天</el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <br />
    <br />
    <br />
    <br />
    <br />

    <br />
    <br />
  </div>
</template>
<style>
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}

#demo {
  width: 100%;
  height: 100px;
  overflow: hidden;
  line-height: 100px;
  font-size: 30px;
  font-family: "黑体";
  background: #ddd url(../assets/head.png) no-repeat 0px center;
  color: #145e2d;
  font-weight: bold;
  margin: 0 auto;
}
#demoin {
  width: 100%;
  height: 200px;
  margin: 0 auto;
  white-space: nowrap;
  overflow: hidden;
}
#demo #demo1,
#demo #demo2 {
  display: inline;
}
</style>

<script>
export default {
  data() {
    return {
      currentDate: new Date().toLocaleDateString()
    };
  }
};
window.onload = function() {
  scrollLeft();
};
function scrollLeft() {
  var speed = 20;
  var tab = document.getElementById("demoin");
  var tab1 = document.getElementById("demo1");
  var tab2 = document.getElementById("demo2");
  tab2.innerHTML = tab1.innerHTML;
  function Marquee() {
    if (tab2.offsetWidth - tab.scrollLeft <= 0) {
      tab.scrollLeft = 0;
    } else {
      tab.scrollLeft++;
    }
  }
  var timer = setInterval(Marquee, speed);
  tab.onmouseover = function() {
    clearInterval(timer);
  };
  tab.onmouseout = function() {
    timer = setInterval(Marquee, speed);
  };
}
</script>
